import React, { useState } from 'react';
import { 
  BookOpen, Play, ChevronRight, ChevronDown, 
  Lightbulb, Target, Zap, Users, Settings,
  Palette, Type, Image, Layers, Eye, Download
} from 'lucide-react';

interface UsageGuideProps {
  onClose: () => void;
}

const UsageGuide: React.FC<UsageGuideProps> = ({ onClose }) => {
  const [activeSection, setActiveSection] = useState<string | null>('getting-started');
  const [activeStep, setActiveStep] = useState<number>(0);

  const sections = [
    {
      id: 'getting-started',
      title: 'Balang',
      icon: Play,
      color: 'bg-green-100 text-green-700',
      steps: [
        {
          title: 'ablon Oluturmaya Balayn',
          description: 'Sol panelden "Kategoriler" sekmesine tklayn ve bir kategori sein.',
          details: [
            'E-ticaret, Moda, Yemek gibi 15+ kategori mevcut',
            'Her kategorinin alt kategorileri var',
            'Kategori seimi ablonunuzun temelini oluturur'
          ]
        },
        {
          title: 'Dinamik Alanlar Ekleyin',
          description: '"Alanlar" sekmesinden placeholder\'lar ekleyin.',
          details: [
            'rn bilgileri: {{product.name}}, {{product.price}}',
            'Marka bilgileri: {{brand.name}}, {{brand.logo}}',
            'letiim bilgileri: {{contact.phone}}, {{contact.email}}',
            'zel alanlar oluturabilirsiniz'
          ]
        },
        {
          title: 'Tasarm Yapn',
          description: 'Canvas zerinde ablonunuzu tasarlayn.',
          details: [
            'Metin, ekil, grsel ekleyin',
            'Renkler, fontlar, boyutlar ayarlayn',
            'Katmanlar dzenleyin',
            'AI ile otomatik tasarm oluturun'
          ]
        }
      ]
    },
    {
      id: 'dynamic-fields',
      title: 'Dinamik Alanlar',
      icon: Target,
      color: 'bg-blue-100 text-blue-700',
      steps: [
        {
          title: 'Placeholder Sistemi',
          description: 'Dinamik alanlar ablonunuzun deiken ksmlarn temsil eder.',
          details: [
            '{{product.name}} - rn ad',
            '{{product.price}} - rn fiyat',
            '{{brand.logo}} - Marka logosu',
            '{{contact.phone}} - letiim telefonu'
          ]
        },
        {
          title: 'Alan Kategorileri',
          description: 'Alanlar kategorilere ayrlmtr.',
          details: [
            'rn: rnle ilgili tm bilgiler',
            'Marka: Marka bilgileri ve logolar',
            'letiim: Telefon, email, adres',
            'Genel: Tarih, kullanc ad vb.'
          ]
        },
        {
          title: 'Validasyon Kurallar',
          description: 'Her alan iin dorulama kurallar belirleyebilirsiniz.',
          details: [
            'Zorunlu alanlar iaretlenebilir',
            'Minimum/maksimum deerler',
            'Format kontrol (email, telefon)',
            'Hata mesajlar zelletirilebilir'
          ]
        }
      ]
    },
    {
      id: 'brand-assignment',
      title: 'Marka Atamas',
      icon: Users,
      color: 'bg-purple-100 text-purple-700',
      steps: [
        {
          title: 'Marka Seimi',
          description: '"Markalar" sekmesinden ablonunuzu atayacanz markalar sein.',
          details: [
            'Marka listesini grntleyin',
            'Kategoriye gre filtreleyin',
            'Arama yapn',
            'oklu seim yapn'
          ]
        },
        {
          title: 'Atama lemi',
          description: 'Seili markalara ablonunuzu atayn.',
          details: [
            'Markalar ablonu kullanabilir',
            'Kendi verilerini girebilir',
            'Otomatik ierik retebilir',
            'statistikleri takip edebilir'
          ]
        },
        {
          title: 'Yetki Ynetimi',
          description: 'Hangi markalarn ablonu grebileceini kontrol edin.',
          details: [
            'Genel: Tm markalar grebilir',
            'zel: Sadece atanan markalar',
            'Marka zel: Sadece belirli markalar',
            'Admin kontrol'
          ]
        }
      ]
    },
    {
      id: 'data-binding',
      title: 'Veri Balama',
      icon: Zap,
      color: 'bg-yellow-100 text-yellow-700',
      steps: [
        {
          title: 'Veri Girii',
          description: '"Veri" sekmesinden placeholder\'lar gerek verilerle doldurun.',
          details: [
            'Her alan iin deer girin',
            'Grsel URL\'leri ekleyin',
            'Tarih formatlarn kontrol edin',
            'Validasyon kurallarna uyun'
          ]
        },
        {
          title: 'rnek Veri Oluturma',
          description: 'Otomatik rnek veri oluturun.',
          details: [
            'API\'den gerek veri ekin',
            'Mock data oluturun',
            'Kategorilere gre veri retin',
            'Hzl test yapn'
          ]
        },
        {
          title: 'nizleme',
          description: 'Verilerle ablonunuzun nasl grneceini kontrol edin.',
          details: [
            'Gerek zamanl nizleme',
            'Farkl cihaz boyutlar',
            'Animasyon kontrolleri',
            'Export seenekleri'
          ]
        }
      ]
    },
    {
      id: 'design-tools',
      title: 'Tasarm Aralar',
      icon: Palette,
      color: 'bg-pink-100 text-pink-700',
      steps: [
        {
          title: 'Temel Aralar',
          description: 'Canvas zerinde tasarm yapmak iin kullanabileceiniz aralar.',
          details: [
            'Metin: Balk, aklama, etiket',
            'ekiller: Dikdrtgen, daire, gen, yldz',
            'Grseller: Logo, fotoraf, ikon',
            'izgiler: Ayrc, dekoratif'
          ]
        },
        {
          title: 'Gelimi zellikler',
          description: 'Profesyonel tasarm iin gelimi zellikler.',
          details: [
            'Glge efektleri',
            'Ke yumuatma',
            'Gradyan renkler',
            'effaflk ayarlar',
            'Dndrme ve lekleme'
          ]
        },
        {
          title: 'AI Tasarm',
          description: 'Yapay zeka ile otomatik tasarm oluturun.',
          details: [
            'Prompt yazarak tasarm isteyin',
            'Stil seenekleri',
            'Otomatik dzen',
            'Renk uyumu',
            'Tipografi nerileri'
          ]
        }
      ]
    },
    {
      id: 'export-share',
      title: 'Da Aktarma & Paylam',
      icon: Download,
      color: 'bg-indigo-100 text-indigo-700',
      steps: [
        {
          title: 'Export Formatlar',
          description: 'ablonunuzu farkl formatlarda da aktarn.',
          details: [
            'PNG: Yksek kalite grsel',
            'JPG: Sktrlm grsel',
            'PDF: Vektr tabanl',
            'JSON: ablon verisi'
          ]
        },
        {
          title: 'Paylam Seenekleri',
          description: 'ablonunuzu bakalaryla paylan.',
          details: [
            'Paylalabilir link',
            'Email ile gnderim',
            'Sosyal medya paylam',
            'QR kod oluturma'
          ]
        },
        {
          title: 'ablon Galerisi',
          description: 'Hazr ablonlar kefedin ve kullann.',
          details: [
            'Kategori bazl ablonlar',
            'Popler ablonlar',
            'Yeni ablonlar',
            'Favori ablonlar'
          ]
        }
      ]
    }
  ];

  const currentSection = sections.find(s => s.id === activeSection);
  const currentStep = currentSection?.steps[activeStep];

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg w-full max-w-6xl h-[90vh] flex overflow-hidden">
        {/* Sidebar */}
        <div className="w-80 bg-gray-50 border-r border-gray-200 p-4 overflow-y-auto">
          <div className="flex items-center justify-between mb-6">
            <h2 className="text-xl font-bold text-gray-900">Kullanm Klavuzu</h2>
            <button
              onClick={onClose}
              className="p-2 hover:bg-gray-200 rounded-full"
            >
              
            </button>
          </div>

          <div className="space-y-2">
            {sections.map((section) => (
              <button
                key={section.id}
                onClick={() => {
                  setActiveSection(section.id);
                  setActiveStep(0);
                }}
                className={`w-full p-3 rounded-lg text-left transition-colors ${
                  activeSection === section.id
                    ? 'bg-white shadow-sm border border-gray-200'
                    : 'hover:bg-gray-100'
                }`}
              >
                <div className="flex items-center gap-3">
                  <div className={`p-2 rounded-lg ${section.color}`}>
                    <section.icon size={16} />
                  </div>
                  <span className="font-medium text-gray-900">{section.title}</span>
                </div>
              </button>
            ))}
          </div>

          {/* Quick Tips */}
          <div className="mt-8 p-4 bg-blue-50 rounded-lg">
            <h3 className="font-semibold text-blue-900 mb-2 flex items-center gap-2">
              <Lightbulb size={16} />
              Hzl pular
            </h3>
            <ul className="text-sm text-blue-800 space-y-1">
              <li> Ctrl+Z ile geri aln</li>
              <li> Shift+Click ile oklu seim</li>
              <li> Space+Click ile pan yapn</li>
              <li> Ctrl+S ile otomatik kaydedin</li>
            </ul>
          </div>
        </div>

        {/* Main Content */}
        <div className="flex-1 flex flex-col">
          {/* Header */}
          <div className="p-6 border-b border-gray-200">
            <div className="flex items-center gap-3 mb-2">
              <div className={`p-2 rounded-lg ${currentSection?.color}`}>
                {currentSection && <currentSection.icon size={20} />}
              </div>
              <h1 className="text-2xl font-bold text-gray-900">{currentSection?.title}</h1>
            </div>
            <p className="text-gray-600">Adm adm rehber ile ablon oluturmay renin</p>
          </div>

          {/* Steps Navigation */}
          <div className="p-4 border-b border-gray-200">
            <div className="flex items-center gap-2">
              {currentSection?.steps.map((_, index) => (
                <button
                  key={index}
                  onClick={() => setActiveStep(index)}
                  className={`w-8 h-8 rounded-full flex items-center justify-center text-sm font-medium ${
                    activeStep === index
                      ? 'bg-blue-600 text-white'
                      : 'bg-gray-200 text-gray-600 hover:bg-gray-300'
                  }`}
                >
                  {index + 1}
                </button>
              ))}
            </div>
          </div>

          {/* Step Content */}
          <div className="flex-1 p-6 overflow-y-auto">
            {currentStep && (
              <div className="max-w-4xl">
                <h2 className="text-2xl font-bold text-gray-900 mb-4">
                  {currentStep.title}
                </h2>
                <p className="text-lg text-gray-700 mb-6">
                  {currentStep.description}
                </p>

                <div className="space-y-4">
                  {currentStep.details.map((detail, index) => (
                    <div key={index} className="flex items-start gap-3 p-4 bg-gray-50 rounded-lg">
                      <div className="w-6 h-6 bg-blue-100 text-blue-600 rounded-full flex items-center justify-center text-sm font-medium flex-shrink-0">
                        {index + 1}
                      </div>
                      <p className="text-gray-700">{detail}</p>
                    </div>
                  ))}
                </div>

                {/* Navigation */}
                <div className="flex items-center justify-between mt-8">
                  <button
                    onClick={() => setActiveStep(Math.max(0, activeStep - 1))}
                    disabled={activeStep === 0}
                    className="flex items-center gap-2 px-4 py-2 text-gray-600 hover:text-gray-800 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    <ChevronRight size={16} className="rotate-180" />
                    nceki
                  </button>

                  <div className="text-sm text-gray-500">
                    {activeStep + 1} / {currentSection?.steps.length}
                  </div>

                  <button
                    onClick={() => setActiveStep(Math.min((currentSection?.steps.length || 1) - 1, activeStep + 1))}
                    disabled={activeStep === (currentSection?.steps.length || 1) - 1}
                    className="flex items-center gap-2 px-4 py-2 text-gray-600 hover:text-gray-800 disabled:opacity-50 disabled:cursor-not-allowed"
                  >
                    Sonraki
                    <ChevronRight size={16} />
                  </button>
                </div>
              </div>
            )}
          </div>
        </div>
      </div>
    </div>
  );
};

export default UsageGuide;
