  'socialMedia': 'Sosyal Medya',
  'channels': 'Kanallar',
  'addChannel': 'Kanal Ekle',
  'editChannel': 'Kanal Dzenle',
  'deleteChannel': 'Kanal Sil',
  'channelName': 'Kanal Ad',
  'channelType': 'Kanal Tr',
  'channelStatus': 'Durum',
  
  // Employee requests
  'employee_requests': 'alan Talepleri',
  'add_employee_request': 'alan Talebi Olutur',
  
  // My employees
  'my_employees': 'alanlarm',
  'my_employees_description': 'Markalarnza bal alanlar grntleyin ve ynetin',
  
